import React from 'react';
import {reduxForm} from "redux-form";
import {createField, Input, Textarea} from "../../../common/FormsControls/FormsControls";
import s from './DescriptionBlock.module.css';
import styles from "../../../common/FormsControls/FormsControls.module.css";

const DescriptionBlockForm = (props) => {
    return (
        <div>
            <form onSubmit={props.handleSubmit}>
                <button>Сохранить</button>
                {props.error &&
                <div className={styles.formSummaryError}>
                    {props.error}
                </div>}
                <div>
                    <div><b>Имя: </b>{createField("Имя", "fullName", [], Input)}</div>
                    <div><b>Ищу работу:</b> {createField("", "lookingForAJob", [], Input, {type: "checkbox"})}</div>
                    <div><b>Описание навыков:</b> {createField("Описание навыков", "lookingForAJobDescription", [], Textarea)}</div>
                    <div><b>Обо мне:</b> {createField("Обо мне", "aboutMe", [], Textarea)}</div>
                    {<div><b>Контакты: </b>{Object.keys(props.userProfile.contacts).map((key) => {
                        return <div className={s.contact}>
                            <b>{key}:</b>{createField(key, "contacts."+key, [], Input)}
                        </div>
                    })}</div>}
                </div>
            </form>
        </div>
    );
}

const DescriptionBlockReduxForm = reduxForm({form: 'edit-profile'})(DescriptionBlockForm)

export default DescriptionBlockReduxForm;
