import s from './DescriptionBlock.module.css';
import React, {useState} from "react";
import Preloader from "../../../../assets/images/preloader/Preloader";
import userPhoto from "../../../../assets/images/user.jpg"
import ProfileStatus from "./ProfileStatus/ProfileStatus";
import DescriptionBlockReduxForm from "./DescriptionBlockForm";

const DescriptionBlock = (props) => {
    const [editMode, setEditMode] = useState(false)
    if (!props.userProfile) {
        return <Preloader />
    }

    const onMainPhotoSelected = (event) => {
        if (event.target.files.length) {
            props.savePhoto(event.target.files[0])
        }
    }

    const onSubmit = (formData) => {
        props.saveProfile(formData)
            .then(() => {
                setEditMode(false)
            })
    }

    const activateEditMode = () => {
        setEditMode(true)
    }

    return (
        <div className={s.descriptionBlock}>
            <div className={s.userInfo}>
                {props.isOwner
                    ?
                    <label className={s.customFileUpload}>
                        <div className={s.userImgContainer}>
                        <img src={props.userProfile.photos.small !== null ? props.userProfile.photos.small : userPhoto}
                             alt=""/>
                        <input type={"file"} onChange={onMainPhotoSelected}/>
                        </div>
                    </label>
                    : <div className={s.userImgContainer}>
                        <img src={props.userProfile.photos.small !== null ? props.userProfile.photos.small : userPhoto}
                             alt=""/>
                    </div>
                }
                <div className={s.userName}>
                    <h3>{props.userProfile.fullName}</h3>
                    <ProfileStatus status={props.status} {...props}/>
                    {editMode
                        ? <DescriptionBlockReduxForm initialValues={props.userProfile} userProfile={props.userProfile} onSubmit={onSubmit} />
                        : <ProfileData {...props} activateEditMode={activateEditMode}/>
                    }
                </div>
            </div>
            <div className={s.socialBlock}>

            </div>
        </div>
    );
}

const ProfileData = (props) => {
    return (
        <div>
            {props.isOwner && <button onClick={props.activateEditMode}>Редактировать</button>}
            <div>
                <div><b>Имя: </b>{props.userProfile.fullName}</div>
                <div><b>Ищу работу:</b> {props.userProfile.lookingForAJob ? "Да" : "Нет"}</div>
                {props.userProfile.lookingForAJob &&
                <div><b>Описание навыков:</b> {props.userProfile.lookingForAJobDescription}</div>
                }
                <div><b>Обо мне:</b> {props.userProfile.aboutMe}</div>
                <div><b>Контакты: </b>{Object.keys(props.userProfile.contacts).map((key) => {
                    return <Contact key={key} contactTitle={key} contactValue={props.userProfile.contacts[key]}/>
                })}</div>
            </div>
        </div>
    )
}

const Contact = ({contactTitle, contactValue}) => {
    return <div className={s.contact}><b>{contactTitle}</b> : {contactValue}</div>
}

export default DescriptionBlock;
